@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  background-image: linear-gradient(154.8deg, #00cfff, #00ffdb, #f5ff7c);
  font-family: "Roboto", sans-serif;
  padding: 0;
  overflow-x: hidden;
}

h1 {
  font-size: 3em;
}

main {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-flow: column nowrap;
}

main p {
  text-align: center;
}

.demos {
  display: flex;
  width: calc(100vw - min(200px, 20vw));
  margin-left: min(100px, 10vw);
  margin-right: min(100px, 10vw);
  padding: 30px;
  overflow-x: auto;
  overflow-y: visible;
  padding: 40px;
}
section.demo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12),
    0 3px 5px -1px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  transition-duration: 0.5s;
  cursor: pointer;
  margin-right: 30px;
}

section.demo:hover {
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14),
    0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
  transition-duration: 0.5s;
}

section.demo img {
  max-width: 50vmin;
  max-height: 50vmin;
  position: relative;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}

article h2 {
  margin-top: 0;
  text-align: center;
}

article p {
  margin-bottom: 0;
}

article {
  position: absolute;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12),
    0 2px 4px -1px rgba(0, 0, 0, 0.2);
  max-width: calc(100% - 50px);
  max-height: calc(100% - 50px);
  opacity: 0.9;
  overflow-y: auto;
  word-wrap: break-word;
  box-sizing: border-box;
  white-space: normal;
}

a {
  color: black !important;
  outline: none;
}

header {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#profilePic {
  width: max(15vmin, 50px);
  border-radius: 50%;
  margin-right: 30px;
  margin-left: 30px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12), 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}

#aboutMe {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
  margin-top: 10vw;
  margin-bottom: 10vw;
}

#meDescription {
  max-width: 80vmin;
  text-align: center;
  max-height: 90vh;
}

.certificates {
  margin-left: min(100px, 10vw);
  margin-right: min(100px, 10vw);
  height: 50vh;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 20px;
  white-space: nowrap;
}

.certificates img {
  max-width: 40vmin;
  max-height: calc(50vh - 70px);
  margin: 10px;
  transition-duration: 0.1s;
  cursor: pointer;
}

.certificates img:hover {
  transform: scale(1.02);
  transition-duration: 0.1s;
}

footer {
  height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  text-align: center;
  justify-content: space-between;
}

.socialIcon {
  width: min(7vw, 32px);
  height: min(7vw, 32px);
}

.socials a:hover {
  box-shadow: 0px 0px 10px 4px rgb(0, 0, 0);
  transform: scale(1.08);
  transition-duration: 0.2s;
}

.socials a {
  width: min(7vw, 32px);
  height: min(7vw, 32px);
  display: inline-block;
  outline: none;
  padding: min(7vw, 16px);
  border-radius: 50%;
  transition-duration: 0.2s;
  pointer-events: all;
  margin-left: 10px;
  margin-right: 10px;
}

.socials {
  display: flex;
  height: 150px;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-90deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-90deg);
  }
}

a[href="https://repl.it/@AlexanderBirabe"]:hover
{
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

